@import url('https://fonts.googleapis.com/css?family=Lato|Roboto:300,400,700&display=swap" rel="stylesheet');
body {
  margin: 0;
  font-family: "Roboto", "Lato", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h1 {
  font-family: "Lato", sans-serif;
  font-size: 3rem;
  font-weight: 400;
  margin-bottom: 0; }
  @media screen and (max-width: 550px) {
    h1 {
      font-size: 2rem; } }

h2 {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  font-weight: 300;
  margin: 1rem auto; }
  @media screen and (max-width: 550px) {
    h2 {
      font-size: 1rem;
      margin: 0.5rem auto; } }

h3 {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  margin: 0 auto;
  text-transform: uppercase; }
